  @import url('https://fonts.googleapis.com/css2?family=Abel&family=Raleway:wght@100&family=Roboto+Slab:wght@900&display=swap');

:root{
  --header-bg: #035765;
  --section-heading: #2F2E41;
  --body-bg: #d8d3d3;
}

body {
  margin: 0;
  padding: 0;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px; 
  background-color: var(--header-bg);
  box-shadow: 0 4px 2px -2px gray;
}

nav > header {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 2rem;
  font-family: 'Roboto Slab', serif;
}


.main{
  margin: 50px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}


.card {
  display: flex;
  flex-direction: row; 
  max-width: 500px;
  padding: 10px;
  align-items: flex-start;
}

.image--container {
  flex-shrink: 0; /* prevents the image container from shrinking */
  margin-right: 30px;
  align-items: flex-start;
}

.image {
  width: 180px; /* Set the width to match your design */
  height: 150px; /* Set the height to match your design */
  object-fit: cover;
  border-radius: 5px;
  max-width: 100%;
}

.info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; /* Aligns content to the top */
  height: 180px; /* Matches the image height */
}


.location {
  display: flex;
  align-items: center; /* Aligns the country and map link in the middle */
  margin-bottom: 0px; /* Adds some space below the location */
  font-size: 0.8em;
}

.country, .date, .description {
  margin: 0;
  color: gray;
  font-family: 'Abel', serif;
}

.city{
  margin: 0;
  color: rgb(98, 98, 98);
  font-family: 'Abel', serif;

}

.title { /* Title style */
  font-size: 1.3em;
  font-family: 'Roboto Slab', serif;
  font-weight: bold;
  margin: 0 0 0 0; /* Adds some space below the title */
}

.description{
  margin: 0;
  color: gray;
  font-family: 'Abel', serif;
  font-size: 0.8em;

}

.date{
  margin: 0;
  color: gray;
  font-family: 'Abel', serif;
  font-size: 0.5em;
}

.map {
  color: #918E9B;
  margin-left: 5px; /* Adds space between the country and map link */
  text-decoration: none; /* Optional: removes underline from links */
  font-family: "Raleway", sans-serif;
}




